// 基于准备好的dom，初始化echarts实例
var myChart = echarts.init(document.getElementById("skills"))

// 指定图表的配置项和数据
var option = {
  tooltip: {},
  radar: {
    shape: "circle",
    name: {
      textStyle: {
        color: "#fff",
        backgroundColor: "#999",
        borderRadius: 3,
        padding: [3, 5],
      },
    },
    indicator: [
      {name: "CSS", max: 100},
      {name: "计算机基础", max: 100},
      {name: "Vue", max: 100},
      {name: "React", max: 100},
      {name: "可视化", max: 100},
      {name: "沟通能力", max: 100},
    ],
  },
  series: [
    {
      name: "能力",
      type: "radar",
      areaStyle: {normal: {}},
      data: [
        {
          value: [75, 90, 70, 85, 80, 95],
          name: "能力雷达",
        },
      ],
    },
  ],
}

// 使用刚指定的配置项和数据显示图表。
myChart.setOption(option)
const width = document.documentElement.clientWidth
if (width > 500) {
  const t = document.querySelector("#tel")
  t.href = "javacript:void('点击复制')"
  let tel = new ClipboardJS("#tel")
  tel.on("success", function (e) {
    console.log("Text:", e.text) // 在控制台输出看一下
    window.alert("复制成功") // 如果用弹窗可自己修改
    e.clearSelection() // 清除文本的选中状态
  })
}
let clipboard = new ClipboardJS("#weChat")
clipboard.on("success", function (e) {
  console.log("Text:", e.text) // 在控制台输出看一下
  window.alert("复制成功") // 如果用弹窗可自己修改
  e.clearSelection() // 清除文本的选中状态
})
